// #region page
.page {
  font-family: "Cinzel", serif;

  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}
// #endregion

// #region header
.header {
  min-width: 320px;
  box-sizing: border-box;
  // padding: 25px 20px 288px;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.header__title {
  margin: 0;
  width: 280px;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  line-height: 40px;
  font-size: 40px;
  font-weight: 700px;
  letter-spacing: -1.5px;

}

.header__bottom {
  padding: 0px 20px 288px;
}
// #endregion

// #top bar region
.top-bar {
  padding: 25px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  display: flex;
  gap: 24px;
}

.top-bar__logo {
  display: flex;
}
// #endregion

// #region menu
.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #D12D35;
  font-weight: 700;
  text-transform: uppercase;
}
.menu__top {
  margin-bottom: 32px;
}

.menu__nav {
  margin-bottom: 32px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__phone-number-avenue {
  margin-bottom: 8px;
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 1px;
}

.menu__phone-number-cloisters {
  margin-bottom: 16px;
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 1px;
}

.menu__call-to-order {
  display: block;
  width: fit-content;

  padding-bottom: 8px;
  border-bottom: 1px solid #fff;

  color: #fff;
  text-decoration: none;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
}
// #endregion
// #region nav
.nav__link {
  color: #fff;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;

}
.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
// #endregion

// #region icons
.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;

}

.icon--phone {
  background-image: url(../images/icons/icon-phone.png);
}

.icon--menu {
  background-image: url(../images/icons/icon-burger-menu.png);
}

.icon--close {
  background-image: url(../images/icons/icon-close.png);
  position: fixed;
  right: 20px;
  top: 36px;
}

// #endregion

// #region footer
.footer {

}

// #endregion
